import React from 'react';

import styles from './kpi-row.module.css';

function KpiRow({ value1, value2, value3, value4, value5, value6 }) {
  return (
    <div className={styles.row}>
      <p className={`${styles.value} ${styles.column1} ${styles.text__left}`}>{value1}</p>
      <p className={`${styles.value} ${styles.column2}`}>{value2}</p>
      <p className={`${styles.value} ${styles.column3}`}>{value3}</p>
      <p className={`${styles.value} ${styles.column4} ${styles.text_red}`}>{value4}</p>
      <p className={`${styles.value} ${styles.column5}`}>{value5}</p>
      <p className={`${styles.value} ${styles.column6} ${styles.text__left}`}>{value6}</p>
    </div>
  );
}

export default KpiRow;
