import { useMediaQuery } from '@mui/material';
import React from 'react';

import { attentionEventsInitial, correctiveEventsInitial } from '../../constants';
import AttentionEvents from '../attention-events/AttentionEvents';
import Card from '../card/Card';
import CorrectiveEvents from '../corrective-events/CorrectiveEvents';
import GanttComponent from '../gantt-component/GanttComponent';
import GeneralInfo from '../general-info/GeneralInfo';
import KeyMetrics from '../key-metrics/KeyMetrics';
import Kpi from '../kpi/Kpi';
import PopupAddEvent from '../popup-add-event/PopupAddEvent';
import ProjectCost from '../project-cost/ProjectCost';
import Team from '../team/Team';
import styles from './main.module.css';

function Main() {
  const [correctiveEvents, setCorrectiveEvents] = React.useState(correctiveEventsInitial);
  const [attentionEvents, setAttentionEvents] = React.useState(attentionEventsInitial);

  const [popupAddCorrectiveEventOpen, setPopupAddCorrectiveEventOpen] = React.useState(false);
  const [popupAddAttentionEventsOpen, setPopupAddAttentionEventsOpen] = React.useState(false);

  const matches = useMediaQuery('(min-width:800px)');

  const handleAddCorrectiveEvent = (value) => {
    setCorrectiveEvents((prev) => [
      ...prev,
      {
        id: correctiveEvents.length + 1,
        value,
      },
    ]);
    setPopupAddCorrectiveEventOpen(false);
  };

  const handleAddAttentionEvent = (value) => {
    setAttentionEvents((prev) => [
      ...prev,
      {
        id: attentionEvents.length + 1,
        value,
      },
    ]);
    setPopupAddAttentionEventsOpen(false);
  };

  return (
    <div className={styles.main}>
      <div className={styles.main__column}>
        <Card title=" ">
          <GeneralInfo />
        </Card>

        <Card title=" ">
          <KeyMetrics />
        </Card>

        <Card title=" ">
          <ProjectCost />
        </Card>

        <Card title=" " onAdd={() => setPopupAddCorrectiveEventOpen(true)}>
          <CorrectiveEvents correctiveEvents={correctiveEvents} />
        </Card>
      </div>

      <div className={styles.main__column}>
        <Card title=" ">
          <Team />
        </Card>

        {matches && (
          <Card title=" ">
            <GanttComponent />
          </Card>
        )}

        <Card title="">
          <Kpi />
        </Card>

        <Card title=" " onAdd={() => setPopupAddAttentionEventsOpen(true)}>
          <AttentionEvents attentionEvents={attentionEvents} />
        </Card>
      </div>

      <PopupAddEvent
        open={popupAddCorrectiveEventOpen}
        onClose={() => setPopupAddCorrectiveEventOpen(false)}
        onSubmit={handleAddCorrectiveEvent}
      />

      <PopupAddEvent
        open={popupAddAttentionEventsOpen}
        onClose={() => setPopupAddAttentionEventsOpen(false)}
        onSubmit={handleAddAttentionEvent}
      />
    </div>
  );
}

export default Main;
