import InsertDriveFileOutlinedIcon from '@mui/icons-material/InsertDriveFileOutlined';
import React from 'react';

import { color } from '../../constants';
import styles from './card.module.css';

function Card({ title, children, onAdd, minWidth }) {
  return (
    <div className={styles.container} style={minWidth && { minWidth: minWidth }}>
      <div className={styles.item__header}>
        <InsertDriveFileOutlinedIcon sx={{ color: color }} />
        <h2 className={styles.item__title}>{title}</h2>
        {onAdd && (
          <button type="button" className={styles.item__add} onClick={onAdd}>
            
          </button>
        )}
      </div>
      {children}
    </div>
  );
}

export default Card;
