import React from 'react';

import ListItem from '../list-item/ListItem';
import styles from './corrective-events.module.css';

function CorrectiveEvents({ correctiveEvents }) {
  return (
    <ul className={styles.events}>
      {correctiveEvents.map((item) => (
        <ListItem key={item.id} item={item} />
      ))}
    </ul>
  );
}

export default CorrectiveEvents;
