import React from 'react';

import ListItem from '../list-item/ListItem';
import styles from './attention-events.module.css';

function AttentionEvents({ attentionEvents }) {
  return (
    <ul className={styles.events}>
      {attentionEvents.map((event) => (
        <ListItem key={event.id} item={event} />
      ))}
    </ul>
  );
}

export default AttentionEvents;
